import { React, useRef, useEffect } from 'react';
import withWrapper from '../../hoc/withWrapper';
import './index.less';

function Snow() {
  // 生成雪花
  const createSnows = () => {
    const snowCount = 200;
    const styleElem = document.createElement('style');
    document.getElementsByTagName('head')[0].appendChild(styleElem);
    const style = document.styleSheets[document.styleSheets.length - 1];

    for (let i = 0; i < snowCount; i++) {
      const span = document.createElement('span');
      span.classList.add('snowflake');
      // 设置雪花飘落样式
      setCssText(span, i, style);
      container.current.appendChild(span);
    }
  }

  // 设置雪花飘落样式
  const setCssText = (elem, index, style) => {
    const random_x = Math.random() * 100;
    const random_offset = Math.random() * 20 - 10;
    const random_x_end = random_x + random_offset;
    const random_x_end_yoyo = random_x + (random_offset / 2);
    const random_yoyo_time = Math.random() * 50 + 30;
    const random_yoyo_y = random_yoyo_time;
    const random_scale = Math.random();
    const random_opacity = Math.random();
    const fall_duration = `${Math.random() * 20 + 10}s`;
    const fall_delay = `${Math.random() * -30}s`;
    const animation_name = `snowFall${index}`;

    const text = {
      transform: `translate(${random_x}em, -12px) scale(${random_scale})`,
      opacity: random_opacity,
      animation: `${animation_name} ${fall_duration} ${fall_delay} linear infinite`,
    }
    for (let key in text) {
      elem.style[key] = text[key];
    }

    // 动画效果样式
    style.insertRule(`@keyframes snowFall${index} {
      ${random_yoyo_time}% {
        transform: translate(${random_x_end}em, ${random_yoyo_y}em) scale(${random_scale});
      }
      to {
        transform: translate(${random_x_end_yoyo}em, 110em) scale(${random_scale});
      }
    }`, style.cssRules.length);
  }

  const container = useRef();
  useEffect(createSnows, []);

  return (
    <section className='snow'>
      <h4>Snow</h4>
      <main ref={container}></main>
    </section>
  )
}

export default withWrapper(Snow);
