import React from "react";
import './wrapper.less';

export default function withWrapper(Component) {
  return class Wrapper extends React.Component {
    state = {fold: false}

    repeat = () => {
      this.setState({fold: !this.state.fold});
      if (!this.state.fold) {
        this.wrapper.classList.add('fold');
      } else {
        this.wrapper.classList.remove('fold');
      }
    }

    render() {
      return (
        <div ref={elem => this.wrapper = elem} className="wrapper">
          <Component />
          {/* <button className="repeat-btn">repeat</button> */}
          <button className="fold-btn" onClick={this.repeat}></button>
        </div>
      )
    }
  }
}
