import { React, useEffect, useRef } from 'react';
import withWrapper from '../../hoc/withWrapper';
import './index.less';

function StaggeredLandInText() {
  const text = useRef();

  useEffect(() => {
    let letters = text.current.textContent.split('');
    text.current.textContent = '';
    letters.forEach((letter, i) => {
      let span = document.createElement('span');
      span.textContent = letter;
      span.style.animationDelay = `${i * 0.05}s`;
      text.current.appendChild(span);
    });
  }, []);

  return (
    <section className='landin-text'>
      <h4>Staggered LandIn Text</h4>
      <main>
        <p ref={text}>Ano hi watashitachi mada shiranai no Fushigi no monogatari desu.</p>
      </main>
    </section>
  );
}

export default withWrapper(StaggeredLandInText);

